function Card({id,name,email}) {
  return (
    <div className="tc bg-light-green dib br3 pa3 ma3 grow bw2 shadow-5 ">
    <img alt="cats" src={`https://robohash.org/${id}?set=set4`} />
    <h2>{name}</h2>
    <h4>{email} </h4>
    </div>
  );
}

export default Card;
