import React from 'react';
// import {cats} from './cats';
import Card from './Card';

function Cardlist({cats}) {
	 
  return (
    <div >
		{cats.map((user, i)=> { // for looping through all the items in the list
    	return (
		    		<Card 
		    		key={cats[i].id} 
		    		id={cats[i].id} 
		    		name={cats[i].name} 
		    		email={cats[i].email}/>
	    		);
			    })
		}
    </div>
  );
}

export default Cardlist;